.profile {
  max-width: 880px;
  margin: auto;
  padding-top: 40px;
  padding-bottom: 51px;
  display: flex;
  align-items: center;
}

@media screen and (max-width: 1050px) {
  .profile {
    flex-direction: column;
    max-width: 88.125%;
    padding-top: 42px;
    padding-bottom: 36px;
  }
}